import React, {useEffect, useRef} from "react";
import styles from "./header.module.scss";
import {Link} from "gatsby";
import {Icon} from "@iconify/react";
import back from "@iconify/icons-mdi-light/arrow-left";
import activatedSensor from "@iconify/icons-mdi-light/sim";
import suspendedSensor from "@iconify/icons-mdi-light/sim-off";
import PropTypes from "prop-types";

const Header = ({showHomeButton, title, sensorActive, setSensorActive, debugPanelActive, setDebugPanelActive}) => {
    const sensorBtn = useRef();
    const titleBtn = useRef();

    useEffect(() => {
        const button = sensorBtn.current;
        const title = titleBtn.current;
        button.addEventListener('click', _ => {
            setSensorActive(!sensorActive);
        });
        title.addEventListener('click', _ => {
            setDebugPanelActive(!debugPanelActive);
        });
        return () => {
            button.removeEventListener('click', _ => {
                setSensorActive(!sensorActive);
            });
            title.removeEventListener('click', _ => {
                setDebugPanelActive(!debugPanelActive);
            });
        }
    });

    return <nav className={styles.header}>
        <button className={styles.backBtn + ' button'} style={{opacity: showHomeButton ? 1 : 0}}><Link to='/' aria-disabled={!showHomeButton}><Icon icon={back}/></Link></button>
        <div className={styles.title} ref={titleBtn}><h1>{title}</h1></div>
        <button className={styles.suspendBtn + ' button'} ref={sensorBtn}><Icon icon={sensorActive ? activatedSensor : suspendedSensor}/></button>
    </nav>;
};

Header.propTypes = {
    title: PropTypes.any,
    meta: PropTypes.any
};

export default Header;
